/* html {
  scroll-behavior: smooth;
} */

.table-content {
  width: 390px;
  height: 80px;
  object-fit: cover;
  border-radius: 40px 40px 0 0;
  margin-bottom: -10px;
}

.table-content-extend {
  width: 390px;
  height: 20px;
  background-color: #2d253a;
}

.projects-ctn {
  opacity: 0;
  transition: 0.5s;
}

.table {
  position: relative !important;
  width: 390px !important;
  margin: auto !important;
}

@media only screen and (max-width: 500px) and (orientation: portrait) {
  .table {
    width: 90vw !important;
  }

  .table-content {
    width: 90vw;
  }

  .table-content-extend {
    width: 90vw;
  }

  .ui.table td .image,
  .ui.table td .image img,
  .ui.table th .image,
  .ui.table th .image img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
  }

  .ui.header > .content {
    padding-left: 0 !important;
  }

  .content {
    text-align: center;
  }
}

.margin-top {
  padding-top: 200px !important;
}

@media only screen and (max-width: 767px) {
  .ui.table:not(.unstackable) tbody {
    display: table-row-group !important;
  }

  .ui.table:not(.unstackable) tr {
    display: table-row !important;
  }

  .ui.table:not(.unstackable) tr > td,
  .ui.table:not(.unstackable) tr > th {
    display: table-cell !important;
  }

  .ui.table:not(.unstackable) thead {
    display: table-header-group;
  }

  .ui.table:not(.unstackable) tr > td,
  .ui.table:not(.unstackable) tr > th {
    padding: 0.78571429em !important;
  }
}

.project-ctn {
  padding-top: 4%;
}

.project-img {
  max-width: 100%;
  display: block;
  margin: auto;
  border-radius: 20px;
}

.table-height-wrapper {
  height: 40vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

@media only screen and (max-width: 767px) {
  .project-ctn {
    padding-top: 15%;
  }

  .wallpaper-project {
    height: 100vh !important;
  }

  .wallpaper-wrapper-project {
    height: 100vh !important;
  }

  .table-height-wrapper {
    height: 50vh;
  }
}

@media only screen and (max-width: 767px) and (orientation: landscape) {
  .project-ctn {
    padding-top: 10%;
  }

  .table-height-wrapper {
    height: 40vh !important;
  }
}

.project-title {
  font-size: 40px;
  font-family: "Raleway", sans-serif;
  margin-top: 60px;
  margin-bottom: 30px;
  text-align: center;
  color: white;
  font-weight: bolder;
}

.project-des {
  text-align: center;
  font-family: "Raleway", sans-serif;
  max-width: 600px;
  padding: 0 20px;
  margin: auto;

  font-size: 18px;
  line-height: 180%;
  color: white;
}

.project-github {
  display: block;
  height: 60px;
  padding: 0 6px;
}

.github-wrapper {
  display: flex;
  justify-content: center;
  margin: 40px 0;
}

.table-wrapper {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 9;
}

.ui.basic.table {
  background: rgba(255, 255, 255, 0.5);
  padding: 30px 30px 0 30px;
  border-radius: 0 0 40px 40px;
  background-color: #2d253a !important;
}

.ui.basic.table * {
  color: white !important;
}

.wallpaper-project {
  height: 80vh;
  width: 100%;
  object-fit: cover;
  position: absolute;
}

.wallpaper-wrapper-project {
  position: relative;
  height: 80vh;
  overflow: hidden;
}

.nightsky {
  background-image: linear-gradient(
    -180deg,
    /* #0d3c8d 0, */ #0d639c 40%,
    #5ea1be 68%,
    #2d253a 70%
  );
}

.moon-project {
  position: absolute;
  top: 5%;
  left: 40%;
  height: 200px;
}

a.linkctn > .image:not(.icon),
.ui.header > img {
  display: inline-block;
  margin-top: 0.14285714em;
  width: 2.5em;
  height: auto;
  vertical-align: middle;
}

.ui.header > .content {
  padding-left: 0.75rem;
  vertical-align: middle;
}

.jumptop {
  position: fixed;
  bottom: 15px;
  right: 15px;
  z-index: 999;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: white;

  display: flex;
  justify-content: center;
  align-items: center;
}

.hand.point.up.icon.big,
.hand.point.up.icon.big:hover {
  color: black;
}

@media only screen and (max-width: 767px) {
  .ui.table:not(.unstackable) tbody,
  .ui.table:not(.unstackable) tr,
  .ui.table:not(.unstackable) tr > td,
  .ui.table:not(.unstackable) tr > th {
    width: 50% !important;
  }
}

.ui[class*="very basic"].table:not(.sortable):not(.striped) td:first-child,
.ui[class*="very basic"].table:not(.sortable):not(.striped) th:first-child {
  padding-left: 0 !important;
}

.play::after {
  content: "";
  background-image: url("pics/projects/playicon.svg");
  background-size: cover;
  display: inline-block;
  height: 100px;
  width: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.project-img-wrapper {
  position: relative;
  max-width: 70%;
  margin: auto;
  border-radius: 20px;
  transition: max-width 0.5s;
}

@media only screen and (max-width: 1199px) {
  .project-img-wrapper {
    max-width: 90%;
  }
}

.static {
  cursor: default;
}
.static::after {
  background-image: none;
}
