* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.home-ctn {
  transition: 0.5s;
  opacity: 0;
}

.wallpaper {
  height: 80vh;
  width: 100%;
  object-fit: cover;
  position: absolute;
}

.wallpaper-back {
  height: 40vh;
  width: 100%;
  position: absolute;
  bottom: -30vh;
  background-color: #2d253a;
}

@media only screen and (orientation: landscape) {
  .wallpaper-back {
    height: 60vw;
    bottom: -50vw;
  }
}

.iso {
  object-position: 50% 100%;
}

div.ui.container.overflow-contain {
  overflow: visible !important;
  overflow-x: visible !important;
  overflow-y: visible !important;
}

.wallpaper-wrapper {
  position: relative;
  height: 80vh;
}

.body {
  max-width: 600px;
  margin: 0 auto;
}

.img-ctn-one {
  display: flex;
  justify-content: center;
  position: relative;
}

#body-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
}

#body-img-ctn {
  width: 250px;
  height: 160px;
  overflow: hidden;
  position: relative;
  border-radius: 10px;

  background-color: rgb(255, 255, 255);
}

#body-text {
  padding: 40px;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  line-height: 160%;
  text-align: center;
}

#hi {
  font-family: "Raleway", sans-serif;
  font-size: 25px;
  margin-bottom: 20px;
  color: #45b29a;
}

.btn-wrapper {
  padding-bottom: 80px;
  margin-top: 20px;
  display: flex;
  justify-content: center;
  position: relative;
}

.divider {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 120px;
}

.dot {
  display: inline-block;
  margin: 0 2px;
  height: 5px;
  width: 5px;
  background-color: rgb(194, 194, 194);
  border-radius: 50%;
}

@media only screen and (max-width: 767px) {
  div.ui.container {
    width: 100% !important;
    margin: 0 !important;
    margin-top: -50px !important;
  }
}

.name {
  font-family: "Indie Flower", cursive;
  font-size: 50px;
  color: #2d253a;

  position: absolute;
  width: 100%;
  text-align: center;
  top: 25%;
}

.logo {
  width: 250px;
  height: 160px;
  padding: 20px;
  object-fit: contain;
}

#logo {
  transition: 1s cubic-bezier(0.74, 0, 0.36, 0.99);
}

#wreathleft {
  position: absolute;
  height: 200px;
  top: 10px;
  right: 46%;
}

#wreathright {
  position: absolute;
  height: 200px;
  top: 10px;
  left: 46%;
}

#browser {
  position: absolute;
  top: -30px;
  height: 200px;
  width: 270px;
  background-color: #d2dcf0;
  border-radius: 20px;
}

#browser-dot {
  position: absolute;
  height: 30px;
  width: 250px;
  top: -30px;
  display: flex;
  align-items: center;
}

#dot-red {
  width: 12px;
  border-radius: 50%;
  height: 12px;
  background-color: #ff6469;
  margin-right: 6px;
}

#dot-yellow {
  width: 12px;
  border-radius: 50%;
  height: 12px;
  background-color: #ffe169;
  margin-right: 6px;
}

#dot-green {
  width: 12px;
  border-radius: 50%;
  height: 12px;
  background-color: #87e1aa;
  margin-right: 6px;
}

#address-bar {
  width: 196px;
  border-radius: 12px;
  height: 12px;
  background-color: #e4eaf6;
}

.speech-bubble {
  position: absolute;
  left: 50%;
  top: -30px;
  background: rgb(219, 219, 219);
  border-radius: 50%;
  height: 100px;
  width: 100px;

  transform: translateX(-200px);
}

.speech-bubble:after {
  content: "";
  position: absolute;
  right: 5px;
  top: 50%;
  width: 0;
  height: 0;
  border: 16px solid transparent;
  border-left-color: rgb(219, 219, 219);
  border-right: 0;
  margin-top: -16px;
  margin-right: -16px;
}

.heart.icon.big {
  position: absolute !important;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.dgreen {
  background-color: #45b29a !important;
}

.mail,
.mail:hover {
  font-family: "Raleway", sans-serif;
  color: #1b7f69;
  font-size: 18px;
  font-weight: lighter;
}

#mail {
  position: absolute;
  height: 80px;
  left: 50%;
  transform: rotate(-10deg) translateX(-50%);
}
