.ui.container {
  overflow: hidden !important;
}

.navbar {
  position: fixed;
  top: 0;
  height: 70px;
  width: inherit;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 999;
}

.navbar * {
  transition: 0s;
}

#profile {
  border-radius: 50%;
  margin-left: 20px;
  height: 60px;
  display: block;
}

li {
  list-style-type: none;
  display: inline-block;
  padding: 20px;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  font-weight: bold;
}

@media only screen and (max-width: 767px) {
  li {
    padding: 8px;
  }
}

.link,
.link:hover {
  color: #061f52;
}

.link {
  background: linear-gradient(0deg, #4dc5ab, #4dc5ab) bottom left no-repeat;
  background-size: 3px 0px;
  transition: 0.4s;
  text-decoration: none;
  padding: 0 8px;
}

.link:hover {
  background-size: 3px 100%;
  padding: 0 8px;
}

.link-active {
  background: linear-gradient(0deg, #4dc5ab, #4dc5ab) bottom left no-repeat;
  background-size: 3px 100%;
  text-decoration: none;
  padding: 0 8px;
}

.ui.container {
  display: block;
  max-width: 100% !important;
}
@media only screen and (max-width: 767px) {
  div.ui.container {
    width: 100% !important;
    margin: 0 !important;
  }
}

.ui.container::after {
  position: fixed;
  content: "";
  top: 0;
  left: 0;
  width: 100vw;
  height: 70px;
  transition: background-color 0.5s, color 0.5s;
  z-index: 998;
}

.nav-active::after {
  background-color: white;
}

.navele-active *,
.navele-active *:hover {
  color: #166150;
}
